@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap');
*{
 font-family: 'Bree Serif', serif;
}

#footer_mainDiv {
    width: 100%;
    height: 300px;
    margin-top: 50px;
}

#footer_mainDiv1 {
    text-align: center;
    width: 100%;
    height: 200px;
}

#footer_mainDiv2 {
    display: flex;
    width: 100%;
    height: 60px;
    margin: auto;
}

#footer_mainDiv3 {
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#footer_aDiv>a {
    font-size: 30px;
    margin-right: 10px;
    color: black;
}
#footer_aDiv>a:hover{
       background: rgb(121, 114, 232);
  background: linear-gradient(
    90deg,
    rgba(121, 114, 232, 1) 0%,
    rgba(91, 91, 236, 1) 44%,
    rgba(26, 150, 236, 1) 99%
  );
}
#footer_aDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    height: 40px;
}

#footer_mainDiv3>p {
    font-size: 16px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}

#footer_head {
    font-size: 28px;
  
}

#footer_head1 {
    margin-top: 10px;
    font-size: 20px;
}

@media screen and (max-width:1180px) and (min-width:760px) {
    #footer_mainDiv3>p {
        font-size: 16px;
    }
}

@media screen and (max-width:759px) and (min-width:200px) {
    #footer_mainDiv3>p {
        font-size: 12px;
    }
}